import React from "react";
import "./Projects.css";

export default class Project extends React.Component {

    render () {
        return (
            <div id="projects">
                <div className="center section-title">
                    Projects
                </div>
                <div class="container">
                    <a target="_blank" rel="noopener noreferrer" href="https://www.estuarylife.ca/">
                        <div className="project-card center pointer" id="estuary-jumbo">
                            <div className="overlay">
                                <div className="project-card-title">Estuary Life</div>
                                <div className="project-card-content">Medicinal marijuan website developed in squarespace
                                </div>
                            </div>
                        </div>
                    </a>
                    <div className="project-card center pointer" id="probably-fine-jumbo">
                        <div className="overlay">
                            <div className="project-card-title">It's Probably Fine</div>
                            <div className="project-card-content">Personal profile website developed in react
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


