import React from 'react';
import $ from 'jquery';

export default class Professional extends React.Component {
    carouselNav (index) {
        console.log($('#skill-carousel'));
        $('#skill-carousel').carousel(index);
    }

    render () {
            return (
                <div id="professional">
                    <div class="center section-title">
                        Professional
                    </div>
                    <div class="container">
                        <div className="card-deck mb-3">
                            <div className="card">
                                <div className="card-header">
                                    Validus Research Inc.
                                </div>
                                <div className="card-body">
                                    <h5 className="card-title">Catastrophe Modeling Application Developer</h5>
                                    <h6 className="card-subtitle mb-2 text-muted">January 2018 - April 2018</h6>
                                </div>
                            </div>
                            <div className="card">
                                <div className="card-header">
                                    CIBC Capital Markets
                                </div>
                                <div className="card-body">
                                    <h5 className="card-title">Data Analyst</h5>
                                    <h6 className="card-subtitle mb-2 text-muted">January 2017 - April 2017</h6>
                                </div>
                            </div>
                            <div className="card">
                                <div className="card-header">
                                    Adknowm
                                </div>
                                <div className="card-body">
                                    <h5 className="card-title">Data Analyst</h5>
                                    <h6 className="card-subtitle mb-2 text-muted">January 2016 - April 2016</h6>
                                </div>
                            </div>
                        </div>
                        <div class="card">
                            <div class="card-header">
                                <ul className="nav">
                                    <li className="nav-item pointer" onClick={() => {this.carouselNav(0)}}>
                                        <a className="nav-link">Skills</a>
                                    </li>
                                    <li className="nav-item pointer" onClick={() => {this.carouselNav(1)}}>
                                        <a className="nav-link">Python</a>
                                    </li>
                                    <li className="nav-item pointer" onClick={() => {this.carouselNav(2)}}>
                                        <a className="nav-link">C++</a>
                                    </li>
                                    <li className="nav-item pointer" onClick={() => {this.carouselNav(3)}}>
                                        <a className="nav-link">C</a>
                                    </li>
                                    <li className="nav-item pointer" onClick={() => {this.carouselNav(4)}}>
                                        <a className="nav-link">SQL</a>
                                    </li>
                                    <li className="nav-item pointer" onClick={() => {this.carouselNav(5)}}>
                                        <a className="nav-link">Web</a>
                                    </li>
                                </ul>
                            </div>
                            <div class="card-body">
                                <div id="skill-carousel" class="carousel slide">
                                    <div class="carousel-inner">
                                        <div class="carousel-item active">
                                            <div className="row mb-3">
                                                <div className="col-3">
                                                    <h5 className="card-title">Python</h5>
                                                    <h6 className="card-subtitle mb-2 text-muted">4 Years of Experience</h6>
                                                    <div className="progress">
                                                        <div className="progress-bar" role="progressbar"
                                                             style={{width: '95%'}}></div>
                                                    </div>
                                                </div>
                                                <div className="col-3">
                                                    <h5 className="card-title">C++</h5>
                                                    <h6 className="card-subtitle mb-2 text-muted">2 Years of Experience</h6>
                                                    <div className="progress">
                                                        <div className="progress-bar" role="progressbar"
                                                             style={{width: '60%'}}></div>
                                                    </div>
                                                </div>
                                                <div className="col-3">
                                                    <h5 className="card-title">C</h5>
                                                    <h6 className="card-subtitle mb-2 text-muted">2 Years of Experience</h6>
                                                    <div className="progress">
                                                        <div className="progress-bar" role="progressbar"
                                                             style={{width: '60%'}}></div>
                                                    </div>
                                                </div>
                                                <div className="col-3">
                                                    <h5 className="card-title">SQL</h5>
                                                    <h6 className="card-subtitle mb-2 text-muted">2 Years of Experience</h6>
                                                    <div className="progress">
                                                        <div className="progress-bar" role="progressbar"
                                                             style={{width: '80%'}}></div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="row mb-3">
                                                <div className="col-4">
                                                    <h5 className="card-title">HTML</h5>
                                                    <h6 className="card-subtitle mb-2 text-muted">2 Year of Experience</h6>
                                                    <div className="progress">
                                                        <div className="progress-bar" role="progressbar"
                                                             style={{width: '95%'}}></div>
                                                    </div>
                                                </div>
                                                <div className="col-4">
                                                    <h5 className="card-title">CSS</h5>
                                                    <h6 className="card-subtitle mb-2 text-muted">2 Year of Experience</h6>
                                                    <div className="progress">
                                                        <div className="progress-bar" role="progressbar"
                                                             style={{width: '75%'}}></div>
                                                    </div>
                                                </div>
                                                <div className="col-4">
                                                    <h5 className="card-title">Javascript</h5>
                                                    <h6 className="card-subtitle mb-2 text-muted">1 Year of Experience</h6>
                                                    <div className="progress">
                                                        <div className="progress-bar" role="progressbar"
                                                             style={{width: '50%'}}></div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="carousel-item" id="carousel-python">
                                            <h5 class="card-title">Python</h5>
                                            <p class="card-text">I've been using Python for 4 years now. Whenever I need to whip up a quick script python is my goto.
                                            I mainly use Jetbrains for python development, but for small projects, lean towards notepad++ and command line. Heres a couple of my favourite libraries: </p>
                                            <p className="card-text">SciKit Learn - Used Scikit learn in <a href="https://www.kaggle.com/competitions">Kaggle's</a> Titanic classification problem</p>
                                            <p class="card-text">Flask - Worked with Flask to define APIs at Validus Research</p>
                                        </div>
                                        <div className="carousel-item" id="carousel-c++">
                                            <h5 className="card-title">C++</h5>
                                            <p class="card-text">I learned C++ during my CS undergrad at the University of Waterloo. I used it extensively throughout my university career
                                            in the following courses: Introduction to Object Oriented Programming, Algorithms, Compilers, and Operating Systems.</p>
                                        </div>
                                        <div className="carousel-item" id="carousel-c">
                                            <h5 class="card-title">C</h5>
                                            <p class="card-text">I learned C as a stepping stone to C++ in CS136 at Waterloo. In addition I also used it throughout my database
                                            course at Waterloo to integrate SQL with the C host language.</p>
                                        </div>
                                        <div className="carousel-item" id="carousel-sql">
                                            <h5 class="card-title">SQL</h5>
                                            <p class="card-text">I learned SQL on my free time and used it throughout my work term at CIBC. In addition I used it a lot
                                            while taking databases at Waterloo</p>
                                        </div>
                                        <div className="carousel-item" id="carousel-web">
                                            <h5 class="card-title">Web</h5>
                                            <p class="card-text">I learned the basics of web development on my free time (HTML, CSS) but got to put my skills to work during my
                                            time at Validus Research. I've had experience with the Bootstrap, VueJs, and React frameworks in addition to easy to use
                                            website deploying services such as Wordpress, Squarespace and Shopify.</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="card-footer">
                                <div class="btn-group" role="group">
                                    <a className="btn btn-outline-primary ml-2" target="_blank" rel="noopener noreferrer"
                                       href="https://github.com/notvincent?tab=repositories">Github</a>
                                    <a className="btn btn-outline-secondary" target="_blank" rel="noopener noreferrer"
                                       href="/src/Resume.pdf" download>Resume</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
    }
}


