import React from 'react';
import './Cover.css'

export default class Cover extends React.Component {
    constructor (props) {
        super(props);
        this.goto = this.goto.bind(this)
    };

    goto (id) {
        document.getElementById(id).scrollIntoView({
            behavior: 'smooth'
        })
    };

    render () {
        return (
            <div class="cover full-screen">
                <div class="cover-title center">
                    <div class="row">
                        <h1>OLIVER DOWD</h1>
                    </div>
                    <div class="row">
                        <div className="col-lg-6 col-md-6 col-sm-6 mb-1">
                            <div class="section-name" onClick={() => {this.goto('professional');}}>
                                Professional
                            </div>
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-6 mb-1">
                            <div className="section-name" onClick={() => {this.goto('projects');}}>
                                Projects
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


